<template>
  <div id="pro-bar-code">
    <div class="left">
      <div class="title">
        <h2 style="text-align:center">产品条码</h2>
      </div>
      <div class="left-content">
        <h3>条码上传</h3>
        <!-- <div class="left-bar-code">
          <el-link
            type="primary"
            v-for="(item, index) in leftBarCodeList"
            :key="index"
            @click="leftBarCodeClick(item[1])"
          >{{index+1+'.'+item[0]}}</el-link>
        </div>-->
        <el-upload
          class="upload-demo"
          drag
          action="123"
          multiple
          :before-upload="beforeUpload"
          :http-request="uploadFile"
          :on-remove="handleRemove"
          ref="upload"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            将文件拖到此处，或
            <em>点击上传</em>
          </div>
        </el-upload>
        <el-button type="primary" size="mini" @click="uploadSubmit"
          >提交</el-button
        >
      </div>
    </div>
    <div class="right">
      <div class="info" style="font-size:16px">
        <div v-if="isShowSort">
          <span v-if="queryForm.checkedType != ''"
            >渠道：{{ queryForm.checkedType }}</span
          >
          <span v-else>渠道：全部</span>
          <span v-if="queryForm.checkedCountry != ''"
            >国家：{{ queryForm.checkedCountry }}</span
          >
          <span v-else>国家：全部</span>
          <span v-if="queryForm.checkedSite != ''"
            >站点：{{ queryForm.checkedSite }}</span
          >
          <span v-else>站点：全部</span>
        </div>
        <div v-else>
          <span>渠道：全部</span>
          <span>国家：全部</span>
          <span>站点：全部</span>
        </div>
      </div>
      <div class="query">
        <el-form ref="queryRef" :model="queryForm" label-width="40px">
          <el-form-item label="渠道">
            <el-select
              v-model="queryForm.checkedType"
              placeholder="请选择"
              clearable
              filterable
            >
              <el-option
                v-for="(item, index) in productTypeList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="国家">
            <el-select
              v-model="queryForm.checkedCountry"
              placeholder="请选择"
              clearable
              @change="countryChange"
              filterable
            >
              <el-option
                v-for="(item, index) in countryList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select
              v-model="queryForm.checkedSite"
              placeholder="请选择"
              clearable
              filterable
            >
              <el-option
                v-for="(item, index) in siteList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="sku">
            <el-select
              v-model="queryForm.checkedSku"
              placeholder="请选择"
              clearable
              filterable
            >
              <el-option
                v-for="(item, index) in skuList"
                :key="index"
                :label="item"
                :value="item"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <el-button type="primary" size="mini" @click="queryInfo"
          >查询</el-button
        >
        <el-button type="primary" size="mini" @click="clearQuery"
          >清空查询</el-button
        >
        <el-button type="primary" size="mini" @click="examine"
          >条码审核</el-button
        >
        <el-button type="danger" size="mini" @click="removeBarCode"
          >删除条码</el-button
        >
        <el-button type="primary" size="mini" @click="exportData"
          >待上传条码数据 一键导出</el-button
        >
      </div>

      <div class="table" style="text-align:center">
        <el-table
          :data="barCodeList"
          border
          style="width: 80%"
          @selection-change="handleSelectionChange"
          ref="codeTableRef"
          @row-click="colRowClick"
          max-height="500"
        >
          <el-table-column type="selection"></el-table-column>
          <el-table-column
            type="index"
            :index="getIndex"
            label="序号"
            width="70"
          ></el-table-column>
          <el-table-column prop="6" label="FNSKU" width="140">
            <template slot-scope="scope">
              <a
                class="pointer"
                :href="
                  'https://www.beyoung.group/tm_data_download/?filename=' +
                    scope.row[5]
                "
                target="_blank"
                >{{ scope.row[6] }}</a
              >
            </template>
          </el-table-column>
          <el-table-column prop="4" label="品名"></el-table-column>
          <el-table-column prop="3" label="sku"></el-table-column>
          <el-table-column prop="0" label="渠道" width="70"></el-table-column>
          <el-table-column prop="1" label="站点" width="70"></el-table-column>
          <el-table-column prop="2" label="国家" width="70"></el-table-column>
          <!-- <el-table-column prop="5" label="商品条码">
            <template slot-scope="scope">
              <a
                class="pointer"
                :href="'https://www.beyoung.group/tm_data_download/?filename='+scope.row[5]"
              >点我下载</a>
            </template>
          </el-table-column>-->
        </el-table>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total,prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
    </div>

    <!-- 审核对话框 -->
    <el-dialog
      title="审核"
      :visible.sync="examineVisible"
      width="80%"
      center
      @close="examineClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <div v-if="checkCodeList.length > 0">
        <el-table
          border
          :data="checkCodeList"
          style="width: 96%"
          @selection-change="examineSelectionChange"
          @row-click="examineRowClick"
          ref="examineTableRef"
          max-height="550"
        >
          <el-table-column type="selection"></el-table-column>
          <el-table-column
            type="index"
            label="序号"
            width="80"
          ></el-table-column>
          <el-table-column
            prop="1"
            label="产品类别"
            width="80"
          ></el-table-column>
          <el-table-column prop="2" label="渠道" width="80"></el-table-column>
          <el-table-column prop="3" label="站点" width="80"></el-table-column>
          <el-table-column prop="4" label="国家" width="100"></el-table-column>
          <el-table-column prop="5" label="sku" width="220"></el-table-column>
          <el-table-column prop="6" label="品名"></el-table-column>
          <el-table-column prop="7" label="商品条码" width="300">
            <template slot-scope="scope">
              <a
                :href="_urlzgl + scope.row[7]"
                class="pointer"
                download
                v-if="scope.row[9] == '新增待审核'"
                >{{ scope.row[7].slice(scope.row[7].lastIndexOf("/") + 1) }}</a
              >
              <a
                :href="
                  'https://www.beyoung.group/tm_data_download/?filename=' +
                    scope.row[7] +
                    '.btw'
                "
                class="pointer"
                download
                v-else
                >{{ scope.row[7].slice(scope.row[7].lastIndexOf("/") + 1) }}</a
              >
            </template>
          </el-table-column>
          <el-table-column prop="8" label="FNSKU" width="120"></el-table-column>
          <el-table-column
            prop="9"
            label="审核状态"
            width="100"
          ></el-table-column>
        </el-table>
      </div>
      <div v-else style="text-align:center">
        <span>暂无需要审核的条码</span>
      </div>
      <span slot="footer" class="dialog-footer" v-if="checkCodeList.length > 0">
        <el-button @click="examineConfirm('审核不通过')">审核不通过</el-button>
        <el-button type="primary" @click="examineConfirm('审核通过')"
          >审核通过</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 查询表单
      queryForm: {
        // 选中的产品类别
        checkedType: "",
        // 选中的站点
        checkedSite: "",
        // 选中的国家
        checkedCountry: "",
        // 选中的sku
        checkedSku: "",
        // 页数
        page: 1,
        // 是否点击了查询
        isQuery: false
      },
      // 产品条码列表
      barCodeList: [],
      // 左边的条码展示列表
      leftBarCodeList: [],
      // 产品类别列表
      productTypeList: ["Amazon"],
      // 站点列表
      siteList: [],
      // 国家列表
      countryList: [],
      // sku列表
      skuList: [],
      // 多选框的选中个数
      selectNum: 0,
      // 选中的多选框 行数据
      selection: [],
      // 选中的文件数量
      fileData: [],
      // 上传的文件
      file: "",
      // 查询文字显示
      isShowSort: false,

      // 审核对话框
      examineVisible: false,
      // 待审核的条码列表
      checkCodeList: [],
      // 审核多选框选中个数
      examineSelectNum: 0,
      // 选中的行数据
      examineSelection: [],

      // 分页相关
      currentPage: 1,
      total: 0,
      pageSize: 50
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 序号
    getIndex(index) {
      return index + 1 + (this.currentPage - 1) * 50;
    },
    // 获取条码列表
    getCodeList() {
      // this.productTypeList = [];
      // this.siteList = [];
      // this.countryList = [];
      this.skuList = [];
      // const page = { page: this.currentPage };
      this.$api.codeQueryInfo(this.queryForm).then(res => {
        console.log(res);
        // 获取条码列表
        this.barCodeList = res.data.data;
        this.skuList = res.data.sku_list;
        // 获取条码所有数量
        this.total = res.data.all_number;
        this.currentPage = 1;
        // 通过条码列表，给查询的下拉框赋值，这样只能查询已有的条码列表
        // for (let item of this.barCodeList) {
        // this.productTypeList.push(item[0]);
        // this.siteList.push(item[1]);
        // this.countryList.push(item[2]);
        // this.skuList.push(item[3]);
        // }
        // 数组去重
        // this.productTypeList = [...new Set(this.productTypeList)];
        // this.siteList = [...new Set(this.siteList)];
        // this.countryList = [...new Set(this.countryList)];
        // this.skuList = [...new Set(this.skuList)];
      });
    },
    initCountry(countrylist) {
      // this.countryList  法国,英国,德国
      let flag = false;
      this.countryList = [];
      for (let key in countrylist) {
        let item = countrylist[key];
        if (
          item === "西班牙" ||
          item === "意大利" ||
          item === "法国" ||
          item === "英国" ||
          item === "德国"
        ) {
          this.flag = true;
          continue;
        }
        this.countryList.push(item);
      }
      if (this.flag) {
        this.countryList.push("欧洲");
      }
    },
    // 获取国家下拉框列表
    getCountryList() {
      this.$api.getCountryList().then(res => {
        console.log(res);
        let countries = res.data.data;
        // 处理国家
        this.initCountry(countries);
      });
    },
    // 国家列表选中获取站点列表
    countryChange(val) {
      this.$api.getSiteList(val).then(res => {
        console.log(res);
        this.siteList = res.data.data;
      });
    },
    // 获取条码框数据
    getCodeSelectList() {
      this.$api.getCodeSelectList().then(res => {
        console.log(res);
        this.leftBarCodeList = res.data.data;
      });
    },
    // 监控多选框事件
    handleSelectionChange(selection) {
      this.selectNum = selection.length;
      this.selection = selection;
      console.log(selection);
    },
    // 批量删除条码
    removeBarCode() {
      if (this.selectNum == 0) {
        this.$message.closeAll();
        return this.$message.info("请选择条码");
      }
      // 得到要删除的id
      let idList = [];
      for (let item of this.selection) {
        idList.push(item[3]);
      }
      console.log(idList);

      this.$confirm("此操作将永久删除条码, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$api.deleteCode(idList).then(res => {
            console.log(res);
            this.getCodeList();
            this.$message.success("删除成功");
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 文件上传前
    beforeUpload(file) {
      console.log(file);
      this.fileData.push(file);
      this.file = file;
    },
    uploadFile(file) {
      // console.log(file);
      // this.fileData.push(file.file);
    },
    // 上传文件
    uploadSubmit() {
      if (this.fileData.length == 0) {
        return this.$message.error("请上传至少一个文件");
      }
      const formData = new FormData();
      // formData.append("file",this.fileData);
      for (let item of this.fileData) {
        formData.append("files", item);
      }
      // formData.append("file", this.file);
      this.$api.upload(formData).then(res => {
        // console.log(res);
        this.getCodeList();
        this.$message.success("上传成功");
        // 上传成功清空文件列表
        this.$refs.upload.clearFiles();
        this.file = "";
        this.fileData = [];
      });
    },
    // 文件移除时触发
    handleRemove(file) {
      // console.log(file);
      // console.log(this.fileData);
      for (let i in this.fileData) {
        if (this.fileData[i] === file.raw) {
          this.fileData.splice(i, 1);
        }
      }
      // console.log(this.fileData);
    },
    // 清空查询条件
    clearQuery() {
      // 清空查询内容
      for (let i in this.queryForm) {
        this.queryForm[i] = "";
      }
      // 上一步会将页数也清空，所以这里要重新赋值
      this.queryForm.page = 1;
      // 重新获取全部数据
      // const page = { page: 1 };
      this.$api.codeQueryInfo(this.queryForm).then(res => {
        console.log(res);
        this.barCodeList = res.data.data;
        this.total = res.data.all_number;
        this.currentPage = 1;
        this.queryForm.isQuery = false;
      });
    },
    // 根据条件查询
    queryInfo() {
      this.queryForm.page = 1;
      this.$api.codeQueryInfo(this.queryForm).then(res => {
        console.log(res);
        this.barCodeList = res.data.data;
        // 表格滚动
        this.$nextTick(() => {
          this.$refs["codeTableRef"].bodyWrapper.scrollTop = 0;
        });
        this.total = res.data.all_number;
        this.currentPage = 1;
        this.isShowSort = true;
        this.queryForm.isQuery = true;
      });
    },
    // 获取待审核的条码类型
    getNoExamimeList() {
      this.$api.getNoExamimeList().then(res => {
        console.log(res);
        this.checkCodeList = res.data.data;
      });
    },
    // 审核条码
    examine() {
      this.examineVisible = true;
      this.getNoExamimeList();
    },
    examineClose() {
      this.examineSelection = [];
      this.examineSelectNum = 0;
    },
    examineConfirm(val) {
      if (this.examineSelection.length == 0) {
        return this.$message.error("请至少选择一个条码");
      }
      let idList = [];
      // 获取id
      for (let item of this.examineSelection) {
        idList.push(item[0]);
      }
      this.$api.examineCode(idList, val).then(res => {
        console.log(res);
        this.$message.success("审核成功");
        this.getCodeList();
        this.examineVisible = false;
      });
    },
    // 审核多选框改变事件
    examineSelectionChange(selection) {
      this.examineSelectNum = selection.length;
      this.examineSelection = selection;
      console.log(selection);
    },
    // 审核表格行被点击时
    examineRowClick(row) {
      console.log(row);
      // 另被点击的行处于选中状态
      this.$refs.examineTableRef.toggleRowSelection(row);
    },
    // 条码列表表格被点击时
    colRowClick(row) {
      this.$refs.codeTableRef.toggleRowSelection(row);
    },
    //左边列表点击 查询
    leftBarCodeClick(barcode_id) {
      const obj = {
        barcode_id
      };
      this.$api.codeQueryInfo(obj).then(res => {
        console.log(res);
        this.barCodeList = res.data.data;
        this.total = res.data.all_number;
        this.currentPage = 1;
        this.isShowSort = true;
      });
    },

    // 分页相关
    // 跳页触发
    handleCurrentChange(val) {
      console.log(val);
      if (this.queryForm.isQuery == false) {
        let obj = {
          // 选中的产品类别
          checkedType: "",
          // 选中的站点
          checkedSite: "",
          // 选中的国家
          checkedCountry: "",
          // 选中的sku
          checkedSku: "",
          // 页数
          page: val,
          // 是否点击了查询
          isQuery: false
        };
        this.$api.codeQueryInfo(obj).then(res => {
          console.log(res);
          this.barCodeList = res.data.data;
          this.total = res.data.all_number;
          this.$nextTick(() => {
            this.$refs["codeTableRef"].bodyWrapper.scrollTop = 0;
          });
        });
      } else {
        this.queryForm.page = val;
        this.$api.codeQueryInfo(this.queryForm).then(res => {
          console.log(res);
          this.barCodeList = res.data.data;
          this.total = res.data.all_number;
          this.$nextTick(() => {
            this.$refs["codeTableRef"].bodyWrapper.scrollTop = 0;
          });
        });
      }
    },

    // 待上传条码数据一键导出
    exportData() {
      this.$api.exportData().then(res => {
        console.log(res);
        let url = window.URL.createObjectURL(new Blob([res.data]));
        let link = document.createElement("a");
        link.href = url;
        link.style.display = "none";
        link.target = "_blank";
        link.setAttribute("download", "未有条码文件数据.xlsx");
        document.body.appendChild(link);
        link.click();
      });
    }
  },
  created() {
    this.getCodeList();
    this.getCodeSelectList();
    this.getCountryList();
  },
  mounted() {}
};
</script>
<style>
#pro-bar-code .query .el-form-item {
  margin: 11px 0;
}
</style>
<style scoped>
#pro-bar-code {
  width: 100vw;
  height: 92vh;
  position: relative;
  /* margin-top: 2vh; */
}
/* #pro-bar-code h2 {
  color: red !important;
  font-size: 36px !important;
} */
#pro-bar-code .title {
  margin-top: 2vh;
}
#pro-bar-code .el-input input[type="number"] {
  border: none;
}
#pro-bar-code .left {
  position: absolute;
  width: 16vw;
  height: 92vh;
  /* background-color: blue; */
  border-right: 1px solid black;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
}
#pro-bar-code .right {
  position: absolute;
  top: 0;
  /* right: 0; */
  width: 80vw;
  height: 92vh;
  background-color: rgba(255, 255, 255, 0.9);
  left: 18vw;
  padding: 24px;
}
#pro-bar-code .left-content {
  margin-top: 2vh;
  text-align: center;
}
#pro-bar-code .left-content h3 {
  margin-bottom: 2vh;
}
#pro-bar-code .left-content .bar-code-list {
  width: 70%;
  height: 28vh;
  border: 1px solid black;
  margin: 0 auto;
  margin-top: 2vh;
  overflow: auto;
}
#pro-bar-code .left-bar-code {
  margin-bottom: 2vh;
}
#pro-bar-code .left-bar-code >>> .el-link {
  display: block;
  margin-bottom: 10px;
}
#pro-bar-code .pointer {
  cursor: pointer;
}
#pro-bar-code .left >>> input[type="file"] {
  display: none;
}
#pro-bar-code .left >>> .el-upload {
  width: 86%;
}
#pro-bar-code .left >>> .el-upload-dragger {
  width: 100%;
}
#pro-bar-code .query {
  margin-top: 2vh;
  margin-bottom: 2vh;
}
#pro-bar-code .query >>> .el-form {
  display: flex;
  justify-content: space-between;
}
#pro-bar-code .info span {
  margin-right: 4vw;
}
#pro-bar-code .el-pagination {
  margin-top: 24px;
}
.el-table {
  margin: 0 auto;
}
</style>



